export default class Main {
  constructor(util, requestAnimationFrame) {
    this.stopSim = false
    this.now = Date.now()
    this.then = Date.now()
    this.fps = 1000
    this.elapsed = null
    this.util = util
    this.requestAnimationFrame = requestAnimationFrame
    console.log('this in constructor is ' + this);
  }

  beginGame(framesPerSecond) {
    if (this.util.getType(framesPerSecond) === '[object Number]') {
      this.fps = framesPerSecond
      this.mainLoop()
    }
  }
  
  mainLoop() {
    console.log('this in mainLoop is ' + this);

    if (this.stopSim) {
      return
    }
  
    this.now = Date.now();
    this.elapsed = this.now - this.then
  
    if (this.elapsed > this.fps) {
      this.update()
      this.render()
      this.then = this.now - (this.elapsed % this.fps)
    }
  
    this.requestAnimationFrame(this.mainLoop)
  }
  
  update() {
    console.log('updating')
  }
  
  render() {
  }

}


